function deepCopy(a) {
  let obj = JSON.parse(JSON.stringify(a));
  return obj;
}

export default {
  memberLogin(user) {
    // 
    // false
    console.log(user);
    return fetch('/member/login/', user, 'POST');
  },
  memberGetVfc(phone) {
    // 
    let res = fetch('/member/verificationCode', {
      mobile: phone,
    }, 'POST');
    return res;
  },

  fetchAnthology(memberId) {
    // 
    return {
      data: [
        {
          id: 1,
          author: 1,
          title: '',
        },
        {
          id: 2,
          author: 1,
          title: '',
        }
      ]
    }
  },

  addAnthology(anthology) {
    anthologyId++
    return {
      data: {
        id: anthologyId,
        author: anthology.author,
        title: anthology.title,
      }
    }
  },

  addArticle(article) {
    contentId++
    return {
      data: {
        id: contentId,
        author: {
          id: article.authorId,
          name: 'wxy',
        },
        type: 1,
        anthology: {
          id: article.anthologyId,
        },
        createTime: +new Date(),
        title: article.title,
        content: article.content,
        readNum: 0,
        praiseNum: 0,
        collectNum: 0,
      }
    }
  },

  addIssue(issue) {
    contentId++
    return {
      data: {
        id: contentId,
        author: {
          id: issue.authorId,
          name: 'wxy',
        },
        type: 2,
        createTime: +new Date(),
        title: issue.title,
        content: issue.content,
        attentionNum: 0,
        praiseNum: 0,
        answerNum: 0,
      }
    }
  },

  fetchContent() {
    let data = deepCopy(contentData);
    console.log(data)
    return {
      data,
    };
  },
  getOneContent(id) {
    console.log(id, contentData)
    let item;
    for (let i = 0, len = contentData.length; i < len; i++) {
      if (contentData[i].id == id) {
        item = deepCopy(contentData[i]);
      }
    }
    item.comment = deepCopy(commentData);
    item.comment = item.comment.filter(the => {
      return the.type === item.type;
    })
    return {
      data: item,
    }
  },


  // comment
  addComment(comment) {
    commentId++;
    let data = {
      id: commentId,
      target: comment.target,
      author: {
        id: comment.authorId,
      },
      type: comment.type,
      createTime: +new Date(),
      content: comment.content,
      pariseNum: 0,
    }
    if (data.type !== 3) {
      data.discussNum = 0;
      data.discuss = [];
    }
    return {
      data,
    }
  },

  getOneComment(id) {
    console.log(id);
    let item;
    for (let i = 0, len = commentData.length; i < len; i++) {
      if (commentData[i].id == id) {
        item = deepCopy(commentData[i]);
      }
    }
    console.log(item)
    return {
      data: item,
    }
  },
};



